.copy-box {
    display: flex;
    align-items: center;
    padding: 8px;
    border-radius: 8px;
    background-color: #56ecf6;
    @include md {
        padding: 6px; }
    input {
        width: 91%;
        line-height: 80px;
        padding-left: 34px;
        font-size: 24px;
        font-family: $main_font;
        font-weight: bold;
        margin-right: 32px;
        @include placeholder-color($main_color);
        border: none;
        background-color: #fff;
        @include md {
            font-size: 18px;
            width: 90%;
            line-height: 60px;
            padding-left: 25px;
            margin-right: 24px; } }
    button {
        background: none; }
    .icon {
        fill: #fff;
        font-size: 28px; } }
