.tokens {
    &__content {}
    &__title {
        font-size: 72px;
        margin-bottom: 23px;
        @include md {
            margin-bottom: 8px; } }
    &__body {}
    &__text {
        margin-bottom: 27px;
        @include md {
            margin-bottom: 25px; }
        p {
            &:not(:last-child) {
                margin-bottom: 36px; }
            @include md {
                &:not(:last-child) {
                    margin-bottom: 22px; } } } }
    .button {
        padding: 21px 128px;
        font-size: 18px;
        &:not(:last-child) {
            margin-right: 44px;
            @include md {
                margin-right: 24px; } }
        @include md {
            padding: 21px 109px;
            border-radius: 32px; }
        &_small {
            padding: 21px 113px;
            @include md {
                padding: 21px 94px; } } }
    .box_turquoise {
        top: -53px;
        left: 778px;
        @include md {
            top: 21px;
            left: auto;
            right: -79px; } }
    .box_blurred {
        top: 192px;
        right: -242px;
        @include md {
            top: auto;
            bottom: -250px;
            right: -185px; } }
    .box_big {
        width: 420px;
        height: 598px;
        top: 500px;
        left: -466px;
        @include md {
            top: auto;
            left: -250px;
            bottom: -720px; } } }

.address {
    padding: 0 8px 0 2px;
    margin-bottom: 48px;
    @include md {
        padding: 0 5px 0 1px;
        margin-bottom: 36px; }
    .title_md {
        margin-bottom: 12px;
        @include md {
            margin-bottom: 19px; } } }


.section-content {
    position: relative;
    padding: 63px 96px 108px;
    background-color: #fff;
    border-radius: 16px;
    box-shadow: 0px 24px 72px 0px rgba(29, 53, 78, 0.25);
    @include md {
        padding: 32px 36px 48px; }
    @include sm {
        padding: 29px 13px 50px;
        border-radius: 8px; } }
